import {Component, NgZone, OnDestroy, OnInit} from '@angular/core';
import {ActionSheetController, AlertController, Events} from '@ionic/angular';
import {ActivatedRoute, Router} from '@angular/router';
import {Product} from '../../shared/product';
import {ProductService} from '../../services/product.service';
import {Subscription} from 'rxjs/index';
import {CategoryService} from '../../services/category.service';
import {Supplier} from '../../shared/supplier';
import {SupplierService} from '../../services/supplier.service';
import {BarcodeScanner} from '@ionic-native/barcode-scanner/ngx';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { ImagePicker, ImagePickerOptions } from '@ionic-native/image-picker/ngx';
import {Location} from '@angular/common';
@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.page.html',
  styleUrls: ['./add-product.page.scss'],
})
export class AddProductPage implements OnInit, OnDestroy {
  subscription: Subscription;
  product: Product;
  aoe = true;
  id: string;
  constructor(private actionSheetController: ActionSheetController, private router: Router,
              private productService: ProductService, public events: Events, private categoryService: CategoryService,
              private alertController: AlertController, private supplierService: SupplierService,
              private zone: NgZone, private barcodeScanner: BarcodeScanner, private camera: Camera,
              private imagePicker: ImagePicker, private activatedRoute: ActivatedRoute, private location: Location) {
      activatedRoute.queryParams.subscribe(queryParams => {
          this.id = queryParams.id;
          if (this.id != undefined) {
              this.aoe = false;
          }
      });
      if (this.aoe) {
          this.initProduct();
      } else {
          this.productService.getProductById(this.id).then((data) => {
              this.product = data.result;
          });
      }
      events.subscribe('category:selected', (category, time) => {
          this.product.category = category;
          this.product.categoryId = category.id;
          this.product.categoryName = category.name;
      });
      events.subscribe('supplier:selected', (supplier, time) => {
          this.product.supplier = supplier;
          this.product.supplierName = supplier.name;
      });
      this.subscription = categoryService.watchCategory().subscribe(
          (activeCategory) => {

          },
          (error) => {

          }
      );
  }
  ngOnInit() {
  }
  initProduct() {
      this.product = {
          id: '',
          name: '',
          categoryId: 0,
          categoryName: '默认分类',
          category: {},
          barcode: '',
          images: [],
          price: '',
          bid: '',
          stock: '',
          spec: '',
          note: '',
          supplierName: '商品供应商',
          supplier: {}
      };
  }
  async onPresentActionSheet() {
      const actionSheet = await this.actionSheetController.create({
          header: '选择您的操作',
          buttons: [
              {
                  text: '拍照',
                  role: 'destructive',
                  handler: () => {
                      this.onCamera();
                  }
              }, {
                  text: '相册',
                  handler: () => {
                      this.onImagePicker();
                  }
              }, {
                  text: '取消',
                  role: 'cancel',
                  handler: () => {
                  }
              }
          ]
      });
      await actionSheet.present();
  }
  async onSave(flag: boolean) {
      if (this.product.name == '' || this.product.category == '' || this.product.price == '') {
          let alert = await this.alertController.create({
              header: '提示',
              message: '带*号为必填，请填写完整！',
              buttons: ['确定']
          });
          alert.present();
      } else {
          this.product.images = [];
          this.productService.insert(this.product).then((data) => {
              if (data.success) {
                  if (flag == true) {
                      this.initProduct();
                  } else {
                      this.addProductSuccess();
                      this.router.navigateByUrl('home');
                  }
              } else {
                  alert('添加失败！');
              }
          });
      }
  }
  async addProductSuccess() {
      let alert = await this.alertController.create({
          header: '提示',
          message: '添加成功！',
          buttons: ['确定']
      });
      alert.present();
  }
  ionViewLeave() {
      this.events.unsubscribe('category:selected');
      this.events.unsubscribe('supplier:selected');
  }
  ngOnDestroy() {
      this.subscription.unsubscribe();
  }
  async presentAlertPrompt() {
      const alert = await this.alertController.create({
          header: '新增供货商',
          inputs: [
              {
                  name: 'name',
                  type: 'text',
                  placeholder: '输入供货商名称'
              },
              {
                  name: 'phone',
                  type: 'number',
                  placeholder: '输入供货商电话'
              }
          ],
          buttons: [
              {
                  text: '取消',
                  role: 'cancel',
                  cssClass: 'secondary',
                  handler: () => {
                      console.log('Confirm Cancel');
                  }
              }, {
                  text: '保存',
                  handler: (data) => {
                      let sup: Supplier = {
                          id: '',
                          name: data.name,
                          phone: data.phone
                      };
                      this.supplierService.insert(sup);
                      this.zone.run(() => {
                          this.product.supplier = sup;
                          this.product.supplierName = data.name;
                      });
                  }
              }
          ]
      });
      await alert.present();
  }
  chooseSuppier() {
    if (this.supplierService.count() > 0) {
        // todo 跳出modal
        this.router.navigateByUrl('supplierList');
    } else {
      this.presentAlertPrompt();
    }
  }
  onScan() {
      this.barcodeScanner.scan().then(barcodeData => {
          console.log('Barcode data', barcodeData);
          this.product.barcode = barcodeData.text;
      }).catch(err => {
          console.log('Error', err);
      });
  }
  onCamera() {
      // let base64Image = 'data:image/png;base64,';
      const options: CameraOptions = {
          quality: 100,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE
      };

      this.camera.getPicture(options).then((imageData) => {
          // imageData is either a base64 encoded string or a file URI
          // If it's base64 (DATA_URL):
          let base64Image = 'data:image/jpeg;base64,' + imageData;
          this.zone.run( () => {
              this.product.images.push(base64Image);
          });
      }, (err) => {
          // Handle error
      });
  }
  onImagePicker() {
      const options: ImagePickerOptions = {
          maximumImagesCount: 4,
          quality: 100
      };
      this.imagePicker.getPictures(options).then((results) => {
          let images = [];
          for (let i = 0; i < results.length; i++) {
              console.log('Image URI: ' + results[i]);
              images.push(results[i]);
          }
          this.product.images = images;
      }, (err) => { });
  }
  async onSaveEdit() {
      if (this.productService.update(this.product)) {
          // 保存成功
          let alert =await this.alertController.create({
              header: '提示',
              message: '保存成功',
              buttons: ['确定']
          });
          alert.present();
          this.router.navigateByUrl('productList');
      }
  }
  back() {
    this.location.back();
  }
}
